<template>
  <div>
    <v-card
      id="user-card"
      class="mx-auto"
      outlined
      :class="{ active: index == currentIndex }"
      v-for="(user, index) in users"
      :key="index"
    >
      <v-list-item two-line>
        <v-list-item-avatar
        tile
        size="80"
        color="grey"
        >
          <v-img :src="require('../assets/images/dummy.jpeg')" class="my-3" contain height="80x" />
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title class="headline mb-1">
            {{ user.firstname + ' ' + user.lastname}}
          </v-list-item-title>
          <v-list-item-subtitle>
            {{ user.email }}
          </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
    </v-card>
    <FloatingActionButton />
  </div>
</template>

<script>
import UsersData from '@/services/usersdata';

// Components
import FloatingActionButton from '@/components/button/FloatingAction.vue';

export default {
  name: 'ListUsers',
  components: {
    FloatingActionButton,
  },
  data() {
    return {
      users: [],
      currentUser: null,
      currentIndex: -1,
      email: '',
    };
  },
  methods: {
    retrieveUsers() {
      UsersData.getAll()
        .then((res) => {
          this.users = res.data;
          console.log(res.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.retrieveUsers();
  },
};
</script>

